import Vue from 'vue'
import { requestCooperative } from '@/api/API'
import BottomBar from '../../components/BottomBar'
import Header from '../../components/Header'
import { Cell } from 'mint-ui'
Vue.component(Cell.name, Cell)

export default {
  name: 'coopAgree',
  components: {
    Header, BottomBar
  },
  data () {
    return {
      flag: 0,
      coopList: []
    }
  },
  mounted () {
    this.getCoopAgree()
  },
  methods: {
    async getCoopAgree () {
      let user = this.$store.state.User || {}
      let res = await requestCooperative(user.token)
      if (res.code === 200) {
        this.coopList = res.data.cooperative
      } else {
        window.EventBus.$emit('globalMessage', res.msg)
      }
    }
  }
}



